import './SkillBar_comp.css';

export default function SkillBar_comp(props) {


console.log(props);
var S = props.skill;

const P = props.lvl;
  
  

  return (

      <div className="row barComponent">
                        <div className="col-2 skillName" >{S}</div>
                        <div className="col-10 container barCont" >
                            <div className="progress barEmpty">
                              <div className="progress-bar barProgress" style={{width:`${P}`}}></div>
                            </div>
                          </div>
                    </div>


);

    
  
}