import './Skills_comp.css';
import Data from '../AppData';
import SkillBar_comp from '../SkillBar-comp/SkillBar_comp';
export default function Skills_comp() {
  var data = new Data().CV_Data;
  var skillBar = data.SkillBar;
  var Bars = []
  for (let i = 0; i < skillBar.length; i++) {
    Bars.push(
      <SkillBar_comp key={i} skill={skillBar[i].skill} lvl={skillBar[i].lvl} />
    )
    
  } 


  var myFocus = data.MyFocus;
  var myFocusContent = [];
  for (let i = 0; i < myFocus.length; i++) {
    myFocusContent.push(
      <div  className="myFocusC">{myFocus[i]}</div>
    )
  } 


  
  

  return (
    <>
     <div className="skillsContainer">
        
        <div className="container">


            <div className="row skillsHeader">
                    <h1>Skills</h1>
            </div>

            <div className="row skillsTitle">
                <span>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Maiores voluptatem optio nostrum sapiente modi eum tempore non sit asperiores rerum omnis, laborum dolorem possimus dolorum nemo, laudantium ex earum sint? Lorem ipsum, dolor sit amet consectetur adipisicing elit. Ullam sed nobis similique, quasi beatae pariatur ab ad modi perspiciatis officia molestiae, impedit asperiores. Blanditiis illum accusamus provident exercitationem expedita ut.</span>
            
        </div>



            <div className="row myFocusMain" >

                <div className="col-md-6 myFocus">
                    <span className="myFocusTitle">MY FOCUS</span>
                    <div className="seperator"> </div>
                    <div className="myFocusContent">
                      {myFocusContent}
                </div>
                </div>

                <div className="col-md-6">

  
                  {Bars}
                  





                   
                </div>

            </div>
        </div>
    </div>

    </>

);

    
  
}