import './Portfolio_comp.css';
import Data from '../AppData';
import PortfolioUnit_comp from '../PortfolioUnit-comp/PortfolioUnit_comp';
export default function Portfolio_comp() {
  var data = new Data().CV_Data;
  var portfolio = data.Portfolio;
  var portfolioCards = [];
  var classFlag = true; 
  for (let i = 0; i < portfolio.length; i++) {
    if (classFlag) {
      portfolioCards.push(
        <PortfolioUnit_comp key={i} title={portfolio[i].title} className={"portfolioCompA"} />
      )
      classFlag = false; 
      
    } else{
      portfolioCards.push(
        <PortfolioUnit_comp key={i} title={portfolio[i].title} className={"portfolioCompB"} />
      )
      classFlag = true; 

    }

    
  }



  
  

  return (


<div class="cont">

        <h1 class="portfolioHeader">Porfolio</h1>

        <div class="flxCont">
            {portfolioCards}

            
        </div>
    </div>



);

    
  
}