import './Cover_comp.css';
import Data from '../AppData';
export default function Cover_comp() {
  var data = new Data().CV_Data;


 //img from local
 //var imgSrc =  require('../img/cover.jpg').default;

 //img from url
 var imgSrc =  data.CoverImg;


 var scrolll = () => 
 {
     var ele = document.getElementById('sss');
     ele.scrollIntoView({behavior: 'smooth'})

 }
  

  return (
//
    <div className="container-fluid cover">
    <img className="coverImg"  width="100%" height="550px" src={imgSrc} />

    <div className="row coverCard">
        <div className="col-md-4">
            <span className="name">{data.Name}</span><br/>
            <span  className="title">{data.Title}</span><br/>
            <button onClick={scrolll} type="button" className="btn btn-outline-primary contactBtn">CONTACT ME</button>

        </div>
    </div>
</div>

);

    
  
}